footer {
  max-width: 1200px;
  width: 100%;
  height: 50px;
  background-color: #ffffff6e;
  border-radius: 22px;
  .footer__wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
    -ms-flex-pack: distribute;
    -webkit-box-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 0 20px;
    .footer__logo {
      width: 50px;
      height: 50px;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
}
